.section {
    margin-top: 16px;
    margin-bottom: 16px;

    &__title {
        @include title-lg;
        padding-top: 160px;
        padding-left: 60px;
        padding-right: 60px;
        padding-bottom: 64px;

        @include less-than('md') {
            padding-top: 80px;
            padding-left: 12px;
            padding-right: 12px;
            padding-bottom: 32px;
        }

    }

}